.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes turtleMove {
  0% {
    left: 0;
  }
  100% {
    left: calc(100% - 60px);
  }
}

@keyframes progressFill {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
.footer {
  background: linear-gradient(135deg, #2d114d, #2b145f); /* match site theme */
  color: #ccc;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
  margin-top: auto;
  width: 100%;
}